<!--  -->
<template>
    <div class="tab">
        <div 
        v-for="(item,index) in tabList"
        :key="index"
        class="tab_item" 
        :class="{active:index===currentIndex}" 
        @click="handleTab(index)"
        >
            <span :class="{color: isColor ,botoom: isBottom}">{{item.text}}</span>
        </div>
    </div>
</template>

<script>

export default {
    name: 'TabControl',
    components: {},
    props: {
        tabList: {
            type: Array,
            default(){
                return []
            }
        },
        isColor: {
            type: Boolean,
            default: true
        },
        isBottom: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            currentIndex: 0
        };
    },
    //方法集合
    methods: {
        handleTab(index){
            this.currentIndex = index;
            this.$emit('handleTabClick',index)
        } 
    },
}
</script>
<style scoped>
.tab{

    display: flex;
    text-align: center;
}
.tab_item{
    flex: 1;
    box-sizing: border-box;
    padding: 10px 0;
    
}
.tab_item.active span{
    padding: 10px 0;
}
.tab_item.active span.color{
    color: var(--color-high-text);
}
.tab_item.active span.botoom{
    border-bottom: 3px solid var(--color-high-text);
}
</style>