<!--  -->
<template>
    <div class="hy_swiper">
        <div class="swiper" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd">
            <slot></slot>
        </div>
        <slot name="indicator"></slot>
        <div class="indicator">
            <slot name="indicator" v-if="showIndicator && slideCount>1">
                <div v-for="(item,index) in slideCount" :key="index" class="indi_item"
                    :class="{active:index===currentIndex-1}">
                </div>
            </slot>
        </div>
    </div>
</template>

<script>

    export default {
        name: "Swiper",
        props: {
            interval: {
                type: Number,
                default: 3000
            },
            animDuration: {
                type: Number,
                default: 300
            },
            moveRatio: {
                type: Number,
                default: 0.25
            },
            showIndicator: {
                type: Boolean,
                default: true
            },
        },
        data() {
            return {
                slideCount: 0, // 元素个数
                currentIndex: 1, // 当前的index
                swiperStyle: {}, // swiper的样式
                totalWidth: 0, // swiper的宽度
                scrolling: false

            };
        },
        mounted() {    
        this.$nextTick(function () {
            setTimeout(() =>{
                // 操作Dom
                this.handleDom();
                // 开启定时器
                this.startTimer();
            },300)
        })
        },
        //方法集合
        methods: {
            // 启用定时器
            startTimer() {
                this.playTimer = window.setInterval(() => {
                    this.currentIndex++;
                    this.scrollContent(-this.currentIndex * this.totalWidth);
                },this.interval)
            },
            // 关闭定时器
            stopTimer() {
                window.clearInterval(this.playTimer)
            },
            // 滚动到正确的位置
            scrollContent(currentPostion) {
                this.scrolling = true;
                // 1.滚动动画
                this.swiperStyle.transition = 'transform '+this.animDuration+'ms';
                this.setTransform(currentPostion);

                // 2.判断滚动到的位置
                this.checkPostion();

                // 3.滚动完成
                this.scrolling = false
            },
            // 判断滚动到的位置
            checkPostion (){
                window.setTimeout(()=>{
                    this.swiperStyle.transition = '0ms';
                    if (this.currentIndex >= this.slideCount + 1) {
                        this.currentIndex = 1;
                        this.setTransform(-this.currentIndex * this.totalWidth)
                    }else if(this.currentIndex <= 0) {
                        this.currentIndex = this.slideCount;
                        this.setTransform(-this.currentIndex * this.totalWidth)                        
                    }
                    // 结束后回调
                    this.$emit('transitionEnd',this.currentIndex-1)
                },this.animDuration)
            },
            // 设置滚动的位置
            setTransform (position) {
                this.swiperStyle.transform = `translate3d(${position}px,0,0)`;
                this.swiperStyle['-webkit-transform'] = `translate3d(${position}px,0,0)`;
                this.swiperStyle['-ms-transform'] = `translate3d(${position}px,0,0)`;
            },
            // 操作DOM，在DOM前后添加slide
            handleDom(){
                // 1.获取操作的元素
                let swiperEl = document.querySelector('.swiper');
                let slideEls = swiperEl.getElementsByClassName('slide');
                // 2.保存个数
                this.slideCount = slideEls.length;
                // 3.如果大于1个，那么前后风别加上一个slide
                if (this.slideCount >1) {
                    
                    let cloneFirst = slideEls[0].cloneNode(true);
                    let cloneLast = slideEls[this.slideCount -1].cloneNode(true);
    
                    swiperEl.insertBefore(cloneLast,slideEls[0])
                    swiperEl.appendChild(cloneFirst);
                    this.totalWidth = swiperEl.offsetWidth;
                    this.swiperStyle = swiperEl.style;
                    
                }
                // 让swiper元素显示第一个元素
                this.setTransform(-this.totalWidth);
            }, 
            // 拖动事件
            touchStart(e){
                // 如果正在滚动，不可以拖动
                if(this.scrolling) return;
                // 停止定时器
                this.stopTimer();
                // 保存滚动的位置
                this.startX = e.touches[0].pageX;
            },
            touchMove(e) {
                this.currentX = e.touches[0].pageX;
                this.distance = this.currentX - this.startX;
                let currentPostion = -this.currentIndex * this.totalWidth;
                let moveDiatance = this.distance + currentPostion
                // 设置当前的位置
                this.setTransform(moveDiatance)
            },
            touchEnd(e) {
                let currentMove = Math.abs(this.distance);

                if (this.distance === 0){
                    return
                } else if (this.distance > 0 && currentMove > this.totalWidth * this.moveRatio) {
                    this.currentIndex--
                } else if (this.distance < 0 && currentMove > this.totalWidth * this.moveRatio) {
                    this.currentIndex++
                }

                // 移动到正确的位置
                this.setTransform(-this.currentIndex * this.totalWidth);

                // 重启定时器
                this.startTimer();
            },
            // 控制前进后退
            previous(){
                this.changeItem(-1)
            },
            next(){
                this,changeItem(1)
            },
            changeItem(num){
                // 1.移除定时器
                this.shopTimer();
                // 2.修改currentIndex和位置
                this.currentIndex+=num;
                this.setTransform(-this.currentIndex * this.totalWidth);
                // 3.添加定时器
                this.startTimer();
            }
        },
    }
</script>
<style>
.hy_swiper{
    position: relative;
    overflow: hidden;
    font-size: 14px;
}
.swiper{
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
}
.indicator{
    position: absolute;
    left: 50%;
    bottom: 5%;
    transform: translateX(-50%);

    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
}
.indi_item{
    box-sizing: border-box;
    width: 0.5em;
    height: 0.5em;
    border-radius: 0.25em;
    margin: 0 0.25rem;
    background-color: #acacac;
}
.indi_item.active{
    background-color: #fff;
    width: 1em;
}
</style>